* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  height: 100%;
  width: 100%;
  font-size: 14px;
}

ul {
  list-style: none;
}

p {
  margin-bottom: 1rem;
  line-height: 1.4em;
  color: $base-color;
  font-family: $paragraph-font;
}

a {
  text-decoration: none;
  color: $base-color-2;
  transition: all 0.5s ease;
  &:hover {
    color: $base-color;
  }
}

.emphasis {
  background-color: lighten($base-color-2, 45%);
  padding: 0 0.25em;
  // background-color: $base-color-2;
  // color: white;
  // color: $emphasis-color;
}

.emphasis::selection {
  color: lighten($base-color-2, 50%);
  background-color: $base-color-2;
}


