@import url(https://fonts.googleapis.com/css?family=Karla|Muli:600|Prompt:500|Roboto:100,300,400|Work+Sans);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box; }

html {
  height: 100%;
  width: 100%;
  font-size: 14px; }

ul {
  list-style: none; }

p {
  margin-bottom: 1rem;
  line-height: 1.4em;
  color: #373738;
  font-family: "Work Sans", sans-serif; }

a {
  text-decoration: none;
  color: #7a7a7a;
  transition: all 0.5s ease; }
  a:hover {
    color: #373738; }

.emphasis {
  background-color: #ededed;
  padding: 0 0.25em; }

.emphasis::-moz-selection {
  color: #fafafa;
  background-color: #7a7a7a; }

.emphasis::selection {
  color: #fafafa;
  background-color: #7a7a7a; }

.nav.show {
  -webkit-transform: translateX(0);
  transform: translateX(0); }

.nav.hide {
  -webkit-transform: translateX(-200px);
  transform: translateX(-200px); }

.nav {
  top: 0;
  z-index: 1;
  background-color: white;
  height: 100vh;
  width: 250px;
  position: fixed;
  display: flex;
  flex-direction: column;
  border-right: 1px solid #e6e6e6;
  transition: all 0.5s ease; }
  .nav ul {
    width: 60%;
    margin: 0 auto;
    list-style: none;
    position: relative;
    top: 20%; }
  @media screen and (min-width: 800px) {
    .nav {
      -webkit-transform: translateX(0);
              transform: translateX(0); } }

.nav-button {
  position: absolute;
  right: 0;
  top: 0;
  padding: 0.8rem;
  font-size: 2rem; }
  @media screen and (min-width: 800px) {
    .nav-button {
      display: none; } }

.nav-item {
  font-family: "Roboto", sans-serif;
  font-weight: 300;
  font-size: 1.1rem;
  text-transform: uppercase;
  letter-spacing: 0.15rem;
  padding-bottom: 0.7rem; }

.nav-item_slide {
  display: inline-block;
  overflow: hidden;
  width: calc(100% + 15px); }

.nav-item_slide span {
  position: relative;
  display: inline-block;
  transition: all 0.4s ease;
  color: #878787;
  padding-left: 5px;
  border-left: 5px solid #7a7a7a;
  -webkit-transform: translateX(-10px);
  transform: translateX(-10px); }
  .nav-item_slide span::before {
    content: attr(data-num);
    display: inline-block;
    padding-right: 0.25em; }
  .nav-item_slide span:hover {
    -webkit-transform: translateX(0);
    transform: translateX(0);
    text-shadow: 0.5px 0 0 #373738;
    color: #373738; }

.index-logo {
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 1.5rem;
  padding: 0.5rem;
  border: 3px solid #2d2d2d;
  font-family: 'Muli';
  font-weight: 600;
  font-size: 2rem;
  color: #2d2d2d; }

.content {
  margin-left: 50px;
  padding-left: 2rem;
  padding-right: 2rem;
  color: #373738;
  height: 100vh; }
  .content div {
    margin-bottom: 2rem; }
  @media screen and (min-width: 800px) {
    .content {
      margin-left: 250px; } }

.container-title, .project-info-page__container-title {
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  width: 100%;
  margin-top: 20vh;
  margin-bottom: 2rem;
  padding-bottom: 0.5rem;
  color: #373738;
  font-size: 3rem;
  text-transform: uppercase;
  letter-spacing: 0.06em;
  border-bottom: 1px solid #373738; }

.container-title[data-num]::before, .project-info-page__container-title[data-num]::before {
  content: attr(data-num);
  display: inline-block;
  padding-left: 10px;
  padding-right: 0.25em;
  border-left: 10px solid #444445; }

.links {
  font-family: "Work Sans", sans-serif; }
  .links ul, .links li {
    display: inline-block; }
  .links li {
    padding-left: 0.8rem; }

.links-label {
  font-weight: bold; }

.project-square {
  position: relative;
  margin: 0 1rem 1rem 0;
  padding: 0.7rem;
  flex: 0 1 280px;
  height: 280px;
  border: 1px solid #e0e0e0;
  border-radius: 10px; }

.project-square__title {
  text-transform: uppercase;
  text-decoration: underline;
  font-size: 1.2rem;
  font-family: "Work Sans", sans-serif;
  font-weight: 400; }

.project-square__image {
  width: 100%;
  height: 180px;
  margin-bottom: 0.8rem;
  object-fit: contain;
  object-position: center center; }

.project-square__link {
  position: absolute;
  bottom: 0.8rem;
  right: 0.8rem; }

.landing-page {
  width: 100vw;
  height: 100vh;
  display: flex;
  position: absolute;
  justify-content: center;
  align-items: center;
  flex-direction: column; }

.landing-page .landing-page-logo {
  font-size: 3.5rem;
  letter-spacing: 0.4rem;
  padding: 1rem; }

.landing-page-enter {
  font-family: "Work Sans", sans-serif;
  font-size: 1rem;
  text-transform: lowercase; }

.projects {
  display: flex;
  flex-flow: wrap;
  max-width: 900px; }

.project-info-page__top {
  margin-top: calc(20vh - 2rem); }

.project-info-page__container-title {
  margin-top: 0.7rem; }
  .project-info-page__container-title::before {
    content: '\A0';
    border-left: 10px solid #444445;
    display: inline-block; }

.project-info-page__back-link {
  color: #7a7a7a;
  font-size: 1.1rem;
  text-transform: lowercase; }
  .project-info-page__back-link svg {
    margin-right: 0.3rem; }

.project-info-page__body {
  display: flex;
  flex-wrap: wrap;
  margin-top: -1.5rem; }

.project-info-page__body > div {
  margin-top: 1.5rem; }

.project-info-page__img {
  max-width: 400px;
  max-height: 250px;
  display: inline-block; }
  .project-info-page__img img {
    height: auto;
    width: 100%; }

.project-info-page__about {
  max-width: 400px;
  flex: 1 1 250px;
  margin: 0 1rem; }
  .project-info-page__about span {
    display: inline-block;
    margin-bottom: 0.5rem;
    font-weight: 700; }

p.project-info-page__comment {
  font-style: italic;
  color: #7a7a7a; }

.project-info-page__tools span {
  font-weight: bold;
  margin-right: 0.8rem; }

